@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,600;1,700;1,800;1,900&display=swap');


html {
  font-size: 62.5%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.ul {
  display: flex;
  gap: 2rem;
}

.ul li {
  font-size: 1.3rem;
}

.common-heading {
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 3rem;
  margin-top: 3rem;
}

input {
  font-weight: 500;
}

.btn {
  font-size: 1.5rem;
  box-sizing: border-box;
  display: inline-block;
  padding: .3rem 1.6rem;

}

.img-center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.img-center NavLink {
  font-size: 1rem;
  font-weight: 600;
}

.margin-7 {
  margin-top: 5rem;
}

.Home {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
  height: 90vh;
  background-image: linear-gradient(to right, rgb(208, 244, 244) 50%, rgb(255, 255, 255) 50%);
}

.Home p {
  color: blue;
  font-size: 2.5rem;
  font-weight: 500;
}

.top {
  width: 100%;
  display: flex;
}

.top-left {
  width: 150px;
  height: 150px;
  margin-right: 30px;
  position: relative;
}

.top-left::after {
  content: "Change Photo";
  bottom: 0;
  left: 0;
  background-color: rgb(141, 141, 141);
  color: white;
  width: 100%;
  text-align: center;
  position: absolute;
  padding: 3px 0;

}

.top-right {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.top-right .name {
  font-size: 1.4rem;
  font-weight: 500;
}

.top-right .profession {
  color: blue;
  font-weight: 400;
}


.top-right .Info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.li {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}

.li p:last-child {
  color: blue;
}

.li p {
  font-weight: 600;
}
.bottom{
  margin-top: 1rem;
}

.bottom .lists{
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.lists-2{
  font-weight: 600;
}

.lists-3{
  color: blue;
}
